<template>
  <div class="category">
    <!-- 头部 -->
    <div class="header clearfix">
      <div class="header_left fl">商品分类</div>
      <div class="header_right fl">
        <van-search shape="round" placeholder="请搜索商品" @focus="goPage('Search')" />
      </div>
    </div>

    <div class="menu-content clearfix">
      <!-- 侧边栏 -->
      <div class="aside-menu fl">
        <div
          class="item"
          :class="{active: item.isActive}"
          v-for="(item, index) in typeData"
          :key="index"
          @click="toggleAsideMenu(item, index)"
        >{{item.cat_name}}</div>
      </div>
      <!-- 内容 -->
      <div class="content">
        <div
          class="content-item clearfix"
          v-for="(item,index) in categories[AsideMenuIndex]"
          :key="index"
        >
          <div class="title">{{item.cat_name}}</div>
          <div class="cou" v-for="(v, i) in item.children" :key="i" @click="goSearch(v,i)">
            <div class="cou_img">
              <img class="auto-img" v-lazy="v.cat_icon" alt />
            </div>
            <div class="cou_name">{{v.cat_name}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //商品
      categories: [],
      AsideMenuIndex: 0,
      //商品类型
      typeData: [],
    };
  },
  created() {
    // 获取商品类型数据
    this.Categories();
  },
  methods: {
    //切换标签
    toggleAsideMenu(item, index) {
      this.AsideMenuIndex = index;
      if (item.isActive) {
        return;
      }

      for (let i = 0; i < this.typeData.length; i++) {
        if (this.typeData[i].isActive) {
          this.typeData[i].isActive = false;
          break;
        }
      }
      item.isActive = true;
    },
    // 获取商品类型数据
    Categories() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        methods: "GET",
        url: "/categories",
      })
        .then((result) => {
          this.$toast.clear();
          if ((result.data.meta.status = 200)) {
            // 添加 isActive属性
            result.data.message.forEach((v, i) => {
              v.isActive = false;
              v.index = i;
            });

            this.AsideMenuIndex = result.data.message.index;
            this.typeData = result.data.message;
            this.typeData[0].isActive = true;

            // 将 children属性推入商品数据
            result.data.message.forEach((v, i) => {
              this.categories.push(v.children);
              // Replace
            });
            // 删除其它分类数据中的无用元素
            this.typeData[this.typeData.length - 1].children.splice(
              0,
              this.typeData[this.typeData.length - 1].children.length - 1
            );
            //页面初始数据
            this.AsideMenuIndex = 0;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //跳转页面
    goPage(name) {
      this.$router.push({ name });
    },
    goSearch(item, index) {
      let keywords = item.cat_name;
      this.$router.push({ name: "Search", query: { keywords } });
    },
  },
};
</script>

<style lang="less" scoped>
.category {
  position: fixed;
  top: 0;
  bottom: 50px;
  left: 0;
  width: 100%;
  .header {
    width: 100%;
    .header_left {
      width: 25%;
      line-height: 50px;
      text-align: center;
      margin-top: 4.3px;
      font-size: 18px;
    }
    .header_right {
      width: 75%;
    }
  }
  .menu-content {
    height: calc(~"100% - 50px");
  }
  .aside-menu {
    width: 100px;
    height: 100%;
    background-color: #f5f5f5;
    overflow-y: auto;
  }
  .item {
    height: 44px;
    text-align: center;
    line-height: 44px;
    font-size: 14px;
    color: rgb(119, 119, 119);
    &.active {
      background-color: #fff;
      color: black;
      font-style: 18px;
      font-weight: 800;
      border-left: 3px solid rgb(255, 204, 0);
    }
  }
  .content {
    padding: 0px 10px;
    width: calc(~"100% - 120px");
    height: calc(100% - 10px);
    overflow-y: auto;
    .content-item {
      margin-top: 7px;
      .title {
        padding: 10px;
        font-size: 15px;
        font-weight: 550;
      }
      .cou {
        width: 28.3%;
        float: left;
        margin: 2.5%;
        .cou_img {
          height: 70px;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .cou_name {
          text-align: center;
          height: 20px;
          line-height: 20px;
          margin-top: 1%;
          font-size: 12px;
          color: #666666;
        }
      }
    }
  }
}
</style>